import Label from "components/Label";
import { IBaseButtonProps } from "components/base/BaseButton/BaseButton";
import { IButtonCommonStyleProps } from "components/interface/Button";
import { SelectButton,SelectButtonProps } from "primereact/selectbutton";
import React from "react";
import { useTranslation } from "react-i18next";
interface ISelectButtonBoxProps extends SelectButtonProps, IButtonCommonStyleProps {

}

const SelectButtonWithoutMemo: React.FC<ISelectButtonBoxProps> = (props: ISelectButtonBoxProps) => {
  const {
    value,
    options,
    onChange,
    className,
    optionLabel,
    optionValue,
    multiple,
    itemTemplate,
    ...primeReactProps
  } = props;
  const { disabled } = primeReactProps;

  const { t } = useTranslation();
  const translateOptions = (options: any) => {
    const translatedOptionsTemp: any = [...options];
    translatedOptionsTemp.forEach((option: any) => {
      if (option.name) {
        option.name = t(option.name);
      }
    });
    return translatedOptionsTemp;
  };

  const translatedOptions = translateOptions(options);

  const defaultItemTemplate=(options:any)=>{
    const isSelected =options.value ===value
    console.log("isSelected", isSelected);
    
    return  <div className={`${isSelected ?" bg-white text-primary-sapphireBlue-600 !rounded-md ":""} py-2 px-9 text-xs flex-shrink-0 `}>{options.name}</div> ;
  }
  const getClassName=()=>{
    return ` p-1 flex justify-center rounded-md text-primary-ptext-1000 bg-primary-gray-200 ${className??""}`
  }
  return (
    <div className="group grid ">
      {/* {!hideLabel && <Label label={label} labelClassNames={labelClassNames} />} */}
      <SelectButton
        value={value}
        options={translatedOptions}
        onChange={onChange}
        className={getClassName()}
        optionLabel={optionLabel}
        optionValue={optionValue}
        multiple={multiple}
        itemTemplate={itemTemplate??defaultItemTemplate}
        disabled={disabled}
        {...primeReactProps}
        //itemTemplate={this.justifyTemplate}
      />
    </div>
  );
};

// Memoization

const SelectButtonBox = React.memo(SelectButtonWithoutMemo);
// redux wiring

// exports
export default SelectButtonBox;
